.container {
  position: relative;
  display: grid;
  min-height: 100%;
  grid-template-rows: 96px 1fr 36px;
  grid-template-columns: 2fr 10fr;
  grid-template-areas:
    'header header '
    'siterbar  task'
    'footer footer ';
}
@media (max-width: 768px) {
  .container {
    grid-template-columns: 1fr;
    grid-template-areas:
      'header '
      'task'
      'footer ';
  }
}
